import { Directive, OnInit, AfterViewInit, ElementRef, HostBinding } from '@angular/core';
@Directive({
  selector:'[DynamicHeight]',
})
export class DynamicHeight implements AfterViewInit {
    constructor(private el:ElementRef) {}
    @HostBinding('style.max-height.px')
    height: number = 0;

    ngAfterViewInit(){

        setTimeout(() =>{
            let intialHeight = 0;
            let childLength = this.el.nativeElement.children.length;
            for(let i=0; i < childLength; i++){
                intialHeight += this.el.nativeElement.children[i].offsetHeight;
            }
            this.height = intialHeight/3;
        },0)
        
    }
}